<template>
  <div class="video-player-wrapper">
    <Hints>
      <template slot="hintName"></template>
      <template slot="hintInfo">
        <p>Vue-Video-PlayerVuevideo.js</p>
        <p><p>github <el-link type="success" href="https://github.com/surmon-china/vue-video-player" target="_blank">Vue-Video-Player</el-link></p>
      </template>
    </Hints>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-card shadow="always">
          <div slot="header" class="title"></div>
          <div class="content-box">
            <video src="https://cdn.jsdelivr.net/gh/baimingxuan/media-store/videos/houlang.mp4" controls width="100%" style="outline: none"></video>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card shadow="always">
          <div slot="header" class="title"></div>
          <div class="content-item">
            <VueVideoPlayer/>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Hints from '../../components/Hints'
import VueVideoPlayer from '../../components/VueVideoPlayer'

export default {
  name: 'VideoPlayer',
  components: { Hints, VueVideoPlayer }
}
</script>

<style lang="less">
.video-player-wrapper {
  .content-box {
    height: 440px;
  }
  .content-item {
    padding: 8px 0;
    .video-js {
      .vjs-big-play-button {
        top: 45%;
        left: 45%;
        font-size: 2em;
      }
    }
  }
}
</style>



