<template>
  <videoPlayer class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"/>
</template>

<script>
import { videoPlayer } from 'vue-video-player'
import 'video.js/dist/video-js.css'

export default {
  name: 'VueVideoPlayer',
  data () {
    return {
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 
        autoplay: false, // true,
        muted: false, // 
        loop: false, // 
        preload: 'auto', // <video>;auto,
        language: 'zh-CN', // 
        aspectRatio: '16:9', // ; - "16:9""4:3"
        fluid: true, // trueVideo.js player;
        sources: [{
          type: 'video/mp4', // 
          src: 'https://cdn.jsdelivr.net/gh/baimingxuan/media-store/videos/houlang.mp4' // 
        }],
        poster: '', // 
        notSupportedMessage: '', // Video.js
        controlBar: {
          timeDivider: true, // 
          durationDisplay: true, // 
          remainingTimeDisplay: false, // 
          fullscreenToggle: true // 
        }
      }
    }
  },
  components: { videoPlayer }
}
</script>



